body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: sans-serif;
  color: #333;
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  background-color: #eee;
}

.app-header img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.app-header h1 {
  margin: 0;
  font-size: 2rem;
}

.app-header p {
  margin: 0;
  font-size: 1.2rem;
}

.app-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.app-nav {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.app-nav-item {
  margin-right: 1rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.app-nav-item.active {
  background-color: #333;
  color: #fff;
}

.app-tab {
  max-width: 600px;
  text-align: justify;
}

.app-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: #eee;
}

.header {
  display: flex;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

.header-avatar {
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  margin-right: 1rem;
}

.header-info {
  margin-right: 5rem;
}

.header-name {
  font-size: 2rem;
  margin: 0;
}

.header-title {
  font-size: 1.2rem;
  margin: 0;
}

.header-links {
  display: flex;
  align-items: right;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 1rem;
  margin-right: 1rem;
}

.header-icon {
  width: 2rem;
  height: 2rem;
  margin-left: 0.5rem;
}
.resume {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0;
  }
  
  .resume h2 {
    font-size: 2.5rem;
    margin-bottom: 50px;
  }
  
  .resume-container {
    max-width: 900px;
    width: 90%;
    margin: 0 auto;
  }
  
  .resume-item {
    margin-bottom: 40px;
  }
  
  .resume-item h4 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .resume-item p {
    font-size: 1rem;
    color: #888;
    margin-bottom: 10px;
  }
  
  .resume-item ul {
    list-style: disc;
    margin-left: 20px;
  }
  
  .resume-item ul li {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
.current-projects-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;
  }
  
  .current-projects-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    margin-top: 20px;
  }
  
  .current-projects-item {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .current-projects-item h3 {
    margin-bottom: 10px;
  }
  
  .current-projects-item p {
    margin-bottom: 20px;
  }
  
  .current-projects-item a {
    text-decoration: none;
    color: #1976d2;
    font-weight: 600;
  }
  
  .current-projects-item a:hover {
    text-decoration: underline;
  }
  
.blog-post {
    margin-bottom: 20px;
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .blog-post:hover {
    background-color: #f2f2f2;
  }
  
  .blog-post h3 {
    margin-top: 0;
    font-size: 24px;
    color: #333;
  }
  
  .blog-post p {
    font-size: 16px;
    line-height: 1.5;
    color: #666;
  }
  
  .blog-post-expanded {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: scroll;
  }
  
  .blog-post-expanded h3 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .blog-post-expanded p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 1;
  }
